<template>
    <div id="game-setting">
        <div class="content">
            <div>
                <img />
                <div>{{ gameName }}</div>
            </div>
            <van-form v-if="[1, 5].indexOf(gameId) > -1">
                <van-cell-group>
                    <van-field 
                        v-model="count"
                        name="训练数量"
                        label="训练数量"
                        placeholder="请输入训练数量">
                        <template #button>
                            <label>个</label>
                        </template>
                    </van-field>
                    <van-field 
                        v-model="time"
                        name="训练时间"
                        label="训练时间"
                        placeholder="请输入训练时间">
                        <template #button>
                            <label>分钟</label>
                        </template>
                    </van-field>
                </van-cell-group>
            </van-form>
            <van-form v-else-if="[4].indexOf(gameId) > -1">
                <van-field 
                    v-model="flashTime"
                    name="闪现时间"
                    label="闪现时间"
                    placeholder="请输入闪现时间">
                    <template #button>
                        <label>毫秒</label>
                    </template>
                </van-field>
            </van-form>
            <van-button @click="start">开始训练</van-button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            gameId: null,
            gameName: "",
            count: 30,
            time: 25,
            flashTime: 800,
        };
    },
    methods: {
        start() {
            let gamePageName = "";
            switch (this.gameId) {
                case 1:
                    {
                        gamePageName = "NumberGame";
                    }
                    break;
                case 2:
                    {
                        gamePageName = "PokerGame";
                    }
                    break;
                case 3:
                    {
                        gamePageName = "ArticleMemoryGame";
                    }
                    break;
                case 4:
                    {
                        gamePageName = "FastEyeScanGame";
                    }
                    break;
                case 5:
                    {
                        gamePageName = "TermsGame";
                    }
                    break;
                case 6:
                    {
                        gamePageName = "CreativeGame";
                    }
                    break;

                default:
                    break;
            }
            if ([1, 5].indexOf(this.gameId) > -1) {
                this.$router.replace({
                    name: gamePageName,
                    params: {
                        count: this.count,
                        time: this.time,
                    },
                });
            } else if ([4].indexOf(this.gameId) > -1) {
                this.$router.replace({
                    name: gamePageName,
                    params: {
                        flashTime: this.flashTime,
                    },
                });
            }
            
        },
    },
    mounted() {
        let { gameId } = this.$route.params;
        this.gameId = Number(gameId);
        switch (this.gameId) {
            case 1:
                {
                    this.gameName = "随机数字游戏";
                }
                break;
            case 2:
                {
                    this.gameName = "扑克游戏";
                }
                break;
            case 3:
                {
                    this.gameName = "文章记忆游戏";
                }
                break;
            case 4:
                {
                    this.gameName = "快眼扫描游戏";
                }
                break;
            case 5:
                {
                    this.gameName = "词语记忆游戏";
                }
                break;
            case 6:
                {
                    this.gameName = "创作游戏";
                }
                break;

            default:
                break;
        }
    },
};
</script>

<style lang="less" scoped>
#game-setting {
    height: 100%;
    background: #eeeeee;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    .content {
        width: 50%;
        background: #FFFFFF;
    }
}
</style>